body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
html{
  background-color: rgb(214, 214, 214);
  
}
.App {
  display: flex;
  flex-direction: column;
  margin: 15px;
}
@media (max-width: 210mm) {
  .App {
    width: 215mm;
  }
}
@media (min-width: 211mm){
  .App {
      align-items: center;
    }
}
.displayNone{
  display: none;
}
.AppPrinting{
  margin: 0;
  align-items: center;
  width: auto;
  max-height: 296mm;
}

@media print{
  @page{
    size: A4 portrait;
    margin: 0 !important;
  }
    body {
      padding: 1px;
      /* This is used to print without headers and footers - margins hide the header and footer text. */
    }
}
:root {
    --cGray: rgb(58, 58, 58);
    --cLightGray: rgb(206, 206, 206);
}

.CVPage{
    width: 210mm;
    height: 297mm;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
}
.CVPage-TopMargin{
    height: 20mm;
}
.CVPage-Content{
    flex-grow: 10;
    display: flex;
    grid-gap: 5mm;
    gap: 5mm;

}
.CVPagePrinting{
    box-shadow:none;
}
.LeftSection{
    width: 80mm;
    align-self: stretch;
    border-right: 2mm solid var(--cGray);
}
.LeftSection-Image{
    width: 80mm;
    margin-top: 0px;
    padding-top: 0;
    border-bottom-right-radius: 30%;
    border-top-left-radius: 30%;
    border-top: 2mm solid var(--cGray);
    border-bottom: 2mm solid var(--cLightGray);
}
.LeftSection-BrTag{
    display: block;
    content: "";
    margin-top: 2mm;
}
.LeftSection-Fieldset{
border:none;
border-top: 0.5mm solid var(--cLightGray);
margin: 5mm 0 0 15mm;
}
.LeftSection-Fieldset legend {
    margin-bottom: 2mm;
    font-weight: bold;
    padding: 0 2mm 0 0 ;
}
.LeftSection-Labels{
    font-weight: bold;
    color: var(--cGray);
}
.LeftSection-InputField{
    border: none;
    width: 62mm;
}
.LeftSection-Margin0{
    margin: 0;
}

/* Language Rating */
.LeftSection-LanguageSubContainer{
    display: flex;
    align-items: center;
}
.LeftSection-LanguageName{
    width: 38mm;
    margin-right: 1mm;
    border:none;
}
.LeftSection-LanguageRating{
    display:flex;
    grid-gap: 1mm;
    gap: 1mm;
}
.LeftSection-LanguageRating div{
    border-radius: 50%;
    height: 2mm;
    width: 2mm;
    border: 0.5mm solid var(--cGray);
    cursor: pointer;
}
@media (hover:hover){
    .LeftSection-LanguageRating:hover div {
        background-color: var(--cLightGray);
    }
    .LeftSection-LanguageRating div:hover{
        background-color: var(--cLightGray);
    }
    .LeftSection-LanguageRating div:hover ~ div {
        background-color: white;
    }
}
.LeftSection-LangLevelColoured{
    background-color: var(--cLightGray);
}
/* Skills */
.LeftSection-SkillsSubContainer{
    display: flex;
    grid-gap: 1mm;
    gap: 1mm;
    align-items: center;
}
.LeftSection-SkillsBulletPoint{
    border-radius: 50%;
    height: 1.5mm;
    width: 1.5mm;
    border: 0.5mm solid var(--cLightGray);
    background-color: var(--cLightGray);
    margin-right: 0.5mm;
}
.LeftSection-SkillsInput{
    border: none;
    width: 58mm;
}
/* Profile */
.LeftSection-ProfileTextArea{
    border: none;
    resize: none;
    overflow: hidden;
    min-height: 15mm;
    margin-bottom: 1mm;
}
.RightSection-H2Container{
    margin: 0;
}
.RightSection-H2{
    border: none;
    font-weight: bold;
    font-size: 38px;
    color: var(--cGray);
    width: 117mm ;
}
.RightSection-Fieldset {
    border:none;
    border-top: 0.5mm solid var(--cLightGray);
    margin: 5mm 1mm 0 0;
}
.RightSection-Fieldset legend {
    font-weight: bold;
    padding: 0 3mm 0 0;
    margin-bottom: 2mm;
}
.RightSection-TextArea{
    border: none;
        resize: none;
        overflow: hidden;
}
.RightSection-TextAreaMinHeight{
min-height: 28mm;
}

/* Experience and Education */
.RightSection-TimelineContainer{
    display: flex;
}
.RightSection-TimelinePoint{
    width: 5mm;
    height: 5mm;
    background-color: var(--cGray);
    border-radius: 50%;
}
.RightSection-TimelineLine{
    width: 0.5mm;
    height:100%;
    margin:auto;
    background-color: var(--cGray);
}
.RightSection-TimelineSubContainer{
    overflow: hidden;
    margin-right: 1mm;
}
.RightSection-TimelineContentContainer{
    display: flex;
    flex-direction: column;
}
.RightSection-TimelineInputField {
    border: none;
    width: 110mm;
}
.RightSection-TimelineInputHeading{
    font-weight: bold;
}
.RightSection-TimelineInputYear{
    color: var(--cGray);
}
.RightSection-TimelineTextArea{
margin-top: 1.5mm;
}
:root {
    --cBlue: #002d6b;
    --cBlueGray: #002d6b33;
    --cOrange: #ff3d3c;
    --cGray: rgb(58, 58, 58);
    --cBackgroundGray: rgb(214, 214, 214);
}
.Settings{
    padding-bottom: 5px;
    width: 210mm;
    color: rgb(58, 58, 58);
    color: var(--cGray);
}
.Settings-TitleAndSub{
    margin-bottom: .7em;
}
.Settings-TitleAndSub-Logo{
    display: flex;
    align-items: flex-end;
    grid-gap: 0.5em;
    gap: 0.5em;
    border-bottom: 2px solid #002d6b;
    border-bottom: 2px solid var(--cBlue);
}
.Settings-TitleAndSub-Logo img {
    height: 1.6em;
    padding: 0.3em;
    border-top: 2px solid #002d6b;
    border-top: 2px solid var(--cBlue); 
    border-left: 2px solid #002d6b; 
    border-left: 2px solid var(--cBlue); 
    border-right: 2px solid #002d6b; 
    border-right: 2px solid var(--cBlue);
    border-top-left-radius: 15%;
    border-top-right-radius: 15%;
}
.Settings-Title{
    color: #002d6b;
    color: var(--cBlue);
    font-size: 2em;
}
.Settings-Title span{
    color: #ff3d3c;
    color: var(--cOrange);
}
.Settings-SubTitle {
    color: #002d6b;
    color: var(--cBlue);
    font-size: 1.2em;
}
.Settings-P{
    margin-bottom: 5px;
}
.Settings-H3{
    margin-bottom: 5px;
    color: #002d6b;
    color: var(--cBlue);
}
.Settings-Fieldset{
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
    margin-bottom: 5px;
    border-radius: 2%;
    border: 0.5mm solid #002d6b33;
    border: 0.5mm solid var(--cBlueGray);
    padding: 0.5mm;
}
.Settings-Fieldset legend {
    font-weight: bold;
    padding: 0 3mm 0 3mm;
    margin-left: 3mm;
}

@media (min-width: 211mm) {
    .Settings-Fieldset legend {
        text-align: center;
        margin-left: 0;
    }
}

.Settings-InputField{
    background-color:rgb(214, 214, 214);
    background-color:var(--cBackgroundGray);
    border: none;
    border-bottom: 0.3mm solid gray;
    width: 100mm;
    margin-left: 3mm;
    color: rgb(58, 58, 58);
    color: var(--cGray);
}
.Settings-Fieldset select {
    background-color: rgb(214, 214, 214);
    background-color: var(--cBackgroundGray);
    margin-left: 5px;
    color: rgb(58, 58, 58);
    color: var(--cGray);
    cursor: pointer;
}
.Settings-Fieldset option:checked,
.Settings-Fieldset option:focus,
.Settings-Fieldset option:active,
.Settings-Fieldset option:hover  {
    background: #002d6b33;
    background: var(--cBlueGray);
    cursor: pointer;
}

.Settings-Btn{
    width: 10em;
    margin: 15px 0;
    margin-right: 10px;
    padding: 0.5em;
    border-radius: 20px;
    font-size: 0.8em;;
    font-weight: bold;
    color: #002d6b;
    color: var(--cBlue);
    border: none;
    background-color: #002d6b33;
    background-color: var(--cBlueGray);
    cursor: pointer;
}
@media(hover:hover){
    .Settings-Btn:hover{
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }
}

.Footer {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 7px;
    gap: 7px;
    margin-top: 10px;
    color: rgba(0, 0, 0, 0.425);
}

.Footer img {
    width: 20px;
    -webkit-filter: invert(20%) sepia(10%) saturate(4%) hue-rotate(342deg) brightness(95%) contrast(90%);
            filter: invert(20%) sepia(10%) saturate(4%) hue-rotate(342deg) brightness(95%) contrast(90%);;
}

.displayNone {
    display: none;
}

